import { Component, OnInit, OnDestroy, Input, OnChanges, SimpleChange, ViewChild } from '@angular/core';
import { Validators, FormBuilder, FormGroup } from '@angular/forms';
import { DatabaseObjectTable } from '../utils/lib/databaseobjecttable';
import { DatabaseObjectColumn } from '../utils/lib/databaseobjectcolumn';
import { DatabaseObjectColumnConst } from '../const/databaseobjectcolumnconst';
import { GSPDBODataTypes } from '../utils/lib/datatype';
import { DataType } from '../utils/lib/datatype';
import { DataBindingDirective, GridComponent, SelectableSettings } from '@progress/kendo-angular-grid';
import { DropDownListComponent } from '@progress/kendo-angular-dropdowns';
import { GridDataResult, DataStateChangeEvent} from '@progress/kendo-angular-grid';
import { Observable } from 'rxjs/Observable';
import { isNullOrUndefined } from 'util';
import { List, Map, fromJS, is } from 'immutable';
import { DatabaseObjectTableView } from '../utils/lib/databaseobjecttableview';
import { DatabaseObjectColumnView } from '../utils/lib/databaseobjectcolumnview';

@Component({
  selector: 'app-columns-manager',
  templateUrl: './columns-manager.component.html',
  styleUrls: ['./columns-manager.component.css']
})
export class ColumnsManagerComponent implements OnInit, OnChanges, OnDestroy {
  @Input() databaseObject: DatabaseObjectTable;
  @Input() currentView: DatabaseObjectTableView;
  @ViewChild('grid') grid: GridComponent;
  @ViewChild('dataType') chooseDataType: DropDownListComponent;
  cols: DatabaseObjectColumn[];
  databaseObjectView: DatabaseObjectTableView;

  public selectedColumns: any[] = [];
  dataTypes = GSPDBODataTypes;
  subscriptions = new Array<any>();

  constructor(private formBuilder: FormBuilder) {
  }
 ngOnInit() {
   this.databaseObjectView = this.currentView;
   if (!isNullOrUndefined(this.databaseObjectView)) {
     this.cols = this.databaseObjectView.columns.toJS();
     this.setColumnIsMultiLanguageProperty();
  }
   this.databaseObject = this.databaseObject;
 }
  ngOnChanges(changes: { [propKey: string]: SimpleChange }) {
    if (changes['databaseObject']) {
      this.databaseObject = changes['databaseObject'].currentValue;
      this.databaseObject = this.databaseObject;
    }
    if (changes['currentView']) {
      this.closeGridCell();
        // viewObject
        this.databaseObjectView = this.currentView;
        this.setColumnIsMultiLanguageProperty();
        this.refresh();
    }
  }
setColumnIsMultiLanguageProperty() {
  if (!isNullOrUndefined(this.databaseObject) && !isNullOrUndefined(this.databaseObject.multiLanguageColumns)) {
    this.databaseObject.multiLanguageColumns.forEach(item => {
      this.setIsMultiLanguage(item, true);
    });
  }
}
  ngOnDestroy(): void {
    this.unsubscribeRefreshView();
  }

  closeGridCell() {
    // 
    this.grid.closeCell();
    // 
    this.grid.cancelCell();
    // 
    this.grid.closeRow(-1);
  }

  public dataStateChange(state: DataStateChangeEvent): void {

  }

  refresh() {
    if (isNullOrUndefined(this.databaseObjectView)) {
     this.cols = null;
      return;
    }
    if (isNullOrUndefined(this.databaseObjectView.columns)) {
      this.cols = null;
    }
    // be
    this.cols = this.databaseObjectView.columns.toJS();
    this.subscribeRefreshView();
  }
  /**
       * 
       */
  private subscribeRefreshView() {
    this.unsubscribeRefreshView();

    // 
    // 
    const refreshViewSubscription = this.databaseObjectView.columns.refreshView.subscribe(
      value => {
        if (isNullOrUndefined(this.databaseObjectView)) {
          this.cols = null;
          return;
        }
        if (isNullOrUndefined(this.databaseObjectView.columns)) {
          this.cols = null;
        }
        this.cols = this.databaseObjectView.columns.toJS();
      }
    );
    this.subscriptions.push(refreshViewSubscription);
  }

  /**
   * 
   */
  private unsubscribeRefreshView() {
    while (this.subscriptions.length > 0) {
      const subscription = this.subscriptions.pop();
      subscription.unsubscribe();
    }
  }

  addColumn() {
 const newColumn = new DatabaseObjectColumn();
    this.addNewcolumn(newColumn);
  }

  private addNewcolumn(item: DatabaseObjectColumn) {
    this.databaseObjectView.columns.add(new DatabaseObjectColumnView(Map(item)));
  }


  deleteColumn() {
    if (this.selectedColumns.length === 0) { alert(''); } else {
      while (this.selectedColumns.length !== 0) {
        const id = this.selectedColumns.pop();
        if (this.canRemove(id)) {
          this.databaseObjectView.columns.removeByID(id);
        }
      }
    }
  }

  /**
     * 
     */
  public confirmHandler({ sender, formGroup, rowIndex, dataItem }) {
    if (formGroup.valid) {
      const ele = this.mergeFormGroupValue(formGroup);
      this.addNewcolumn(ele);
      sender.closeRow(rowIndex);
    }
  }
  /**
   * 
   * @param formGroup
   */
  private mergeFormGroupValue(formGroup: FormGroup): DatabaseObjectColumn {
    if (isNullOrUndefined(formGroup)) {
      alert(''); // 
    }
    const ele = formGroup.value['column'];
    ele.code = formGroup.value['code'];
    ele.name = formGroup.value['name'];
    ele.id = formGroup.value['id'];
    ele.length = formGroup.value['length'];
    ele.precision = formGroup.value['precision'];
    ele.dataType = formGroup.value['dataType'];
    ele.scale = formGroup.value['scale'];
    ele.isUnique = formGroup.value['isUnique'];
    ele.isNullable = formGroup.value['isNullable'];
    ele.ifPrimaryKey = formGroup.value['ifPrimaryKey'];
    ele.defaultValue = formGroup.value['defaultValue'];
    return ele;
  }
  /**
   * 
   */
  public cancelAddNewItemHandler(args: any) {
    const { sender, rowIndex } = args;
    sender.closeRow(rowIndex);
  }

  /**
 * 
 * @param id
 */
  private canRemove(id: string) {
    if (!isNullOrUndefined(this.databaseObject.columns.find(item => item.id === id))) {
      alert('');
      return false;
    }
    const indexes = this.currentView.indexes.toJS();
    let isCanRemove = true;
    if (!isNullOrUndefined(indexes) && indexes.length > 0) {
     indexes.forEach(index => {
      if (!isNullOrUndefined(index.columns.find(column => column.id === id))) {
        alert('');
        isCanRemove = false;
        return;
      }
    });
    }
    return isCanRemove;
  }
  /**
      * 
      * @param param0
      */
  public cellClickHandler(args: any) {
    const { sender, rowIndex, columnIndex, column, dataItem, isEdited, isNew } = args;

    // 
    if (rowIndex === -1) {
      return;
    }
    // 
    this.databaseObjectView.columns.setSelectIndexByID(dataItem.id);

    //  
    switch (column.field) {
      // 
      case DatabaseObjectColumnConst.Code:
        if (!this.CanChangeCode(dataItem)) {
          return;
        }
        // return,editCell
        break;
      // 
      case DatabaseObjectColumnConst.DataType:
        if (!this.CanChangeDataType(dataItem)) {
          return;
        }
        // return,editCell
        break;
      // ,
      case DatabaseObjectColumnConst.IsMultiLanguage:
        break;
      // /
      case DatabaseObjectColumnConst.Length:
        if (!this.CanChangeLength(dataItem)) {
          // alert('');
          return;
        }
        // return,editCell
        break;
      case DatabaseObjectColumnConst.Precision:
        if (!this.CanChangePresicion(dataItem)) {
          // alert('');
          return;
        }
        // return,editCell
        break;
      case DatabaseObjectColumnConst.Scale:
        if (!this.CanChangeScale(dataItem)) {
          // alert('');
          return;
        }
        // return,editCell
        break;
      case DatabaseObjectColumnConst.IsNullable:
      case DatabaseObjectColumnConst.IsUnique:
      case DatabaseObjectColumnConst.IfPrimaryKey:
          return;
    }
    if (!isEdited) {
      sender.editCell(rowIndex, columnIndex, this.createFormGroup(dataItem));
      return;
    }
  }

  private createFormGroup(column: any): FormGroup {
    return this.formBuilder.group({
      'code': [column.code],
      'name': [column.name],
      'length': [column.length, Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,4}')])],
      'precision': [column.precision, Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,3}')])],
      'scale': [column.scale, Validators.compose([Validators.required, Validators.pattern('^[0-9]{1,2}')])],
      'dataType': this.getDataTypeWithUdt(column),
      'isUnique': [column.isUnique],
      'isNullable': [column.isNullable],
      'ifPrimaryKey': [column.ifPrimaryKey],
      'defaultValue': [column.defaultValue],
      'id': [column.id], // ID
      'column': [column], // 
    });
  }


  private getDataTypeWithUdt(ele: DatabaseObjectColumn) {
      return ele.dataType;
  }
  /**
   * 
   * @param args
   */
  public cellCloseHandler(args: any) {
    const { sender, formGroup, dataItem, rowIndex, column } = args;

    if (!formGroup.valid) {
      // 
      args.preventDefault();
    } else if (formGroup.dirty) {
      const propertyName = column.field;
      const newValue = formGroup.value[propertyName];
      // 
      this.changeCurrentProperty(propertyName, dataItem, newValue);
      // []
      this.changeRelativeProperty(propertyName, dataItem, newValue);
    }
  }

  changeCurrentProperty(propertyName: string, dataItem: DatabaseObjectColumn, newValue: any): any {
    //  
    this.updateValueByElementId(dataItem.id, propertyName, newValue);
  }

  /**
   * []
   * @param propertyName 
   * @param dataItem 
   * @param newValue 
   */
  private changeRelativeProperty(propertyName: string, dataItem: DatabaseObjectColumn, newValue: any) {
    //  /
    if (!isNullOrUndefined(this.getViewElementById(dataItem.id))) {
      const isMultiLanguage = this.getViewElementById(dataItem.id).get(DatabaseObjectColumnConst.IsMultiLanguage);
      if (isMultiLanguage) {
      // 1-1 
      if (propertyName === DatabaseObjectColumnConst.DataType) {
        if (newValue !== DataType.Varchar && newValue !== DataType.Clob) {
          this.setIsMultiLanguage(dataItem.id, false);
        }
      }
    }
    }
    //  
    // 
    if (propertyName === DatabaseObjectColumnConst.Code && newValue.length > 0) {
      const currentElement = this.databaseObjectView.columns.getItemByID(dataItem.id) as DatabaseObjectColumnView;
      const ID = currentElement.get(DatabaseObjectColumnConst.ID) as string;
      if (isNullOrUndefined(ID) || ID.length === 0) {
        currentElement.update(DatabaseObjectColumnConst.ID, oldValue => newValue);
      }
    }

    //  
    if (propertyName === DatabaseObjectColumnConst.DataType) {
      this.changeLengthPrecisionByType(newValue, dataItem);
    }
  }

  /**
 * []
 * @param isMultiLanguage 
 * @param column 
 */
  changeIsMultiLanguageCheck(isMultiLanguage: boolean, column: DatabaseObjectColumn) {
    // 
    // 1.[]
    // 2.IDID,ParentID,
    if (isMultiLanguage === true) {
      if (column.dataType !== DataType.Char && column.dataType !== DataType.Varchar && column.dataType !== DataType.Clob
        && column.dataType !== DataType.NChar && column.dataType !== DataType.NVarchar && column.dataType !== DataType.NClob) {
        alert('[][][][NChar][NVarchar][NClob]');
        this.setIsMultiLanguage(column.id, false);
        return;
      }
      if (this.isIDType(column)) {
        alert('');
        this.setIsMultiLanguage(column.id, false);
        return;
      }
    } else {
      const currentColumn = this.databaseObject.columns.find(item => item.id === column.id);
      const currentColumn1 = this.databaseObject.multiLanguageColumns.find(item => item === column.id);
      if (!isNullOrUndefined(currentColumn) && !isNullOrUndefined(currentColumn1)) {
        alert('');
        this.setIsMultiLanguage(column.id, true);
        return;
      }
    }
    this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IsMultiLanguage, isMultiLanguage);
  }

  /**
 * []
 * @param IsUnique 
 * @param column 
 */
  changeIsUniqueCheck(isUnique: boolean, column: DatabaseObjectColumn) {
   const columns = this.currentView.columns.toJS();
   const primaryKeyColumns = [];
    columns.forEach(item => {
      if (item.ifPrimaryKey) {
        primaryKeyColumns.push(primaryKeyColumns);
      }
    });


    if (!isUnique && column.ifPrimaryKey && primaryKeyColumns.length === 1) {
       alert('');
      this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IsUnique, true);
      return;
    }
    this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IsUnique, isUnique);
  }

  /**
* []
* @param isNullable 
* @param column 
*/
  changeIsNullableCheck(isNotNull: boolean, column: DatabaseObjectColumn) {
    if (column.ifPrimaryKey) {
      alert('');
      this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IsNullable, false);
      return;
    }
      this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IsNullable, !isNotNull);
    if (isNotNull) {

      this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IsNullable, false);
    }
  }

  /**
* []
* @param ifPrimaryKey 
* @param column 
*/
  changeIfPrimaryKeyCheck(ifPrimaryKey: boolean, column: DatabaseObjectColumn) {
    const columns = this.currentView.columns.toJS();
    let countPK = 0;
    columns.forEach(columnPK => {
      if (columnPK.ifPrimaryKey) {
        countPK = countPK + 1;
      }
    });
    if (ifPrimaryKey === true) {
      if (column.isMultiLanguage) {
        alert('');
        this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IfPrimaryKey, false);
        return;
      }
      if (!column.isUnique) {
        this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IsUnique, true);
      }
      if (column.isNullable) {
        this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IsNullable, false);
      }

    } else {
      if (countPK === 2 && !ifPrimaryKey) {
        this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IfPrimaryKey, false);
        const columns1 = this.currentView.columns.toJS();
        const elseColumnWithPK = columns1.find(elseColumn => elseColumn.ifPrimaryKey === true);
        if (!elseColumnWithPK.IsUnique) {
          this.updateValueByElementId(elseColumnWithPK.id, DatabaseObjectColumnConst.IsUnique, true);
        }
      }
    }
    this.updateValueByElementId(column.id, DatabaseObjectColumnConst.IfPrimaryKey, ifPrimaryKey);
  }


  /**
    * 
    * @param elementId ID
    */
  private setIsMultiLanguage(elementId: string, isMultiLanguage: boolean) {
    this.updateValueByElementId(elementId, DatabaseObjectColumnConst.IsMultiLanguage, isMultiLanguage);
  }

  /**
   * 
   */
  changeLengthPrecisionByType(type: DataType, dataItem: DatabaseObjectColumn) {
    if (type !== dataItem.dataType ) {
      const length = this.dataTypes.find(item => item.value === type).length;
      const precision = this.dataTypes.find(item => item.value === type).precision;
      this.updateValueByElementId(dataItem.id, DatabaseObjectColumnConst.Length, length);
      this.updateValueByElementId(dataItem.id, DatabaseObjectColumnConst.Precision, precision);
    }
  }

  /**
 * ID
 * @param elementId ID
 * @param propertyName 
 * @param propertyValue 
 */
  updateValueByElementId(elementId: string, propertyName: string, propertyValue: any) {
    this.databaseObjectView.columns.updateByID(elementId, elementView => {
      elementView.update(propertyName, oldValue => propertyValue);
      return elementView;
    });
  }

  /**
       * 
       * @param id ID
       */
  public getViewElementById(id: string) {
    return this.databaseObjectView.columns.getItemByID(id);
  }

  /**
* 
* @param column 
*/
  CanChangeCode(column: DatabaseObjectColumn): boolean {
   if (!isNullOrUndefined(this.databaseObject.columns.find(item => item.id === column.id))) {
      return false;
    }
    return true;
  }
  /**
 * (id)
 * @param column 
 */
  CanChangeDataType(column: DatabaseObjectColumn): boolean {
    // --22-0
    if (this.isIDType(column)) {
      if (column.dataType !== DataType.Varchar) {
        return true;
      } else {
        // alert('ID');
        return false;
      }
    }
    return true;
  }

  /**
   * (1.ID2.)
   * @param column 
   */
  CanChangeLength(column: DatabaseObjectColumn): boolean {
    if (this.isIDType(column)) {
      // alert('ID');
      return false;
    }
    return this.dataTypes.find(item => item.value === column.dataType).canChangeLength;
  }

  /**
   * (1.ID2.)
   * @param column 
   */
  CanChangePresicion(column: DatabaseObjectColumn): boolean {
    if (this.isIDType(column)) {
      // alert('ID');
      return false;
    }
    return this.dataTypes.find(item => item.value === column.dataType).canChangePresicion;
  }
  CanChangeScale(column: DatabaseObjectColumn): boolean {
    return this.dataTypes.find(item => item.value === column.dataType).canChangeScale;
  }
  CanChangeNullConstraint(column: DatabaseObjectColumn): boolean {
    if (column.ifPrimaryKey) {
      // alert('ID');
      return false;
    }
    return true;
  }

  CanChangeUniqueConstraint(column: DatabaseObjectColumn): boolean {
    if (column.ifPrimaryKey) {
      // alert('ID');
      return false;
    }
    return true;
  }
  CanChangePrimaryKey(column: DatabaseObjectColumn): boolean {
    return true;
  }
  /**
 * id
 * id,parentId,
 * @param column 
 */
  private isIDType(column: DatabaseObjectColumn) {

      return column.ifPrimaryKey;
  }

  tabSelectChange(args: any) {

  }
  removeColumn(args: any) {

  }
  addHandler(args: any) {

  }
}



