/**
 * 
 */
enum DataType {

    /**
    * 
    */
    Char= 0,

    /**
     * 
     */
    Varchar= 1,

    /**
     * 
     */
    Clob= 2,

    /**
     * 
     */
    Int= 3,

    /**
     * 
     */
    Decimal= 4,

    /**
     * 
     */
    DateTime= 5,

    /**
     * 
     */
    TimeStamp= 6,

    /**
     * 
     */
    Blob= 7,
    /**
     * Unicode
     */
    NChar= 8,
    /**
     * Unicode
     */
    NVarchar= 9,
    /**
     * Unicode
     */
    NClob= 10
}

export { DataType };


export const GSPDBODataTypes:
    Array<{
        text: string, value: DataType | string,
        canChangeLength: boolean, canChangePresicion: boolean, canChangeScale: boolean, length: number, precision: number, scale: number
    }> = [
        {
            text: '', value: DataType.Varchar, canChangeLength: true,
            canChangePresicion: false, canChangeScale: false, length: 22, precision: 0, scale: 0
        },
        {
            text: '', value: DataType.Clob, canChangeLength: false,
            canChangePresicion: false, canChangeScale: false, length: 0, precision: 0, scale: 0
        },
        {
            text: '', value: DataType.Int, canChangeLength: false,
            canChangePresicion: false, canChangeScale: false, length: 0, precision: 0, scale: 0
        },
        {
            text: '', value: DataType.Decimal, canChangeLength: true,
            canChangePresicion: true, canChangeScale: true, length: 0, precision: 18, scale: 0
        },
        {
            text: '', value: DataType.Char, canChangeLength: false,
            canChangePresicion: false, canChangeScale: false, length: 1, precision: 0, scale: 0
        },
        {
            text: '', value: DataType.DateTime, canChangeLength: false,
            canChangePresicion: false, canChangeScale: false, length: 0, precision: 0, scale: 0
        },
        {
            text: '', value: DataType.TimeStamp, canChangeLength: false,
            canChangePresicion: false, canChangeScale: false, length: 0, precision: 0, scale: 0
        },
        {
            text: '', value: DataType.Blob, canChangeLength: false,
            canChangePresicion: false, canChangeScale: false, length: 0, precision: 0, scale: 0
        },
        {
            text: 'NChar', value: DataType.NChar, canChangeLength: true,
            canChangePresicion: false, canChangeScale: false, length: 0, precision: 0, scale: 0
        },
        {
            text: 'NVarchar', value: DataType.NVarchar, canChangeLength: true,
            canChangePresicion: false, canChangeScale: false, length: 0, precision: 0, scale: 0
        },
        {
            text: 'NClob', value: DataType.NClob, canChangeLength: false,
            canChangePresicion: false, canChangeScale: false, length: 0, precision: 0, scale: 0
        }
    ];






