export enum DboType {
    /// <summary>
    /// 
    /// </summary>
    Table = 0,

    /// <summary>
    /// 
    /// </summary>
    View = 1 ,

    /// <summary>
    /// 
    /// </summary>
    Function = 2,

    /// <summary>
    /// 
    /// </summary>
    StoreProcedure = 5
}

export const DboTypes: Array<{ index: number, text: string, value: DboType }> = [
    // { index: 0, text: '', value: DboType.Table}
    { index: 1, text: '()', value: DboType.View},
    { index: 2, text: '()', value: DboType.Function},
    { index: 5, text: '()', value: DboType.StoreProcedure }
];



